import { lazy } from "react";

import { Navigate } from "react-router-dom";

const Layout = lazy(() => import("../components/layout"));
const Home = lazy(() => import("../page/home"));
const Price = lazy(() => import("../page/price"));
const Events = lazy(() => import("../page/events"));
const Webinars = lazy(() => import("../page/webinars"));
const CaseStudies = lazy(() => import("../page/casestudies"));
const HelpCenter = lazy(() => import("../page/helpcenter"));
const routes = [
  {
    path: "/",
    element: <Layout />,
    children: [
      {
        path: "/home",
        element: <Home />,
      },
      {
        path: "/price",
        element: <Price />,
      },
      {
        path: "/events",
        element: <Events />,
      },
      {
        path: "/webinars",
        element: <Webinars />,
      },
      {
        path: "/helpcenter",
        element: <HelpCenter />,
      },
      {
        path: "/casestudies",
        element: <CaseStudies />,
      },
      {
        path: "/",
        element: <Navigate to="/home" />,
      },
    ],
  },
  {
    path: "*",
    element: <Navigate to="/home" />,
  },
];

export default routes;
